
.accordion{
  border-top: 5px solid map-get($theme-colors, 'success' ) !important;
  border-radius: 10px !important;
}
.accordion-item{
  border-color: #E7E9ED !important;
}
// .accordion-button::after{
//   background: url('/assets/img/icons/Path.svg') !important;
//   background-position: center !important;
//   background-repeat: no-repeat !important;
// }
.accordion-button:focus{
  box-shadow: 0 0 0 0.25rem rgba(104, 213, 133, 0.25) !important;
}
.accordion-button:not(.collapsed){
  box-shadow: none !important;
}

.main-container{
  min-height: 100vh;
  overflow: hidden;
  position: relative;
  .hero{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 90vh;
    display: flex;
    z-index: 0;
    align-items: center;
    margin-bottom: 10px;
    
    .button-text{
      color: #F4F7FA;
    }
    .action-button{
      position: relative;
      z-index: 1120;
    }
  }
  .hero-image {
    .chat-image{
      width: 80%;
    }
    .dots{
      top: -5%;
      right: 15%;
      z-index: -10;
    }
    @include media-breakpoint-up(lg){
      .chat-image{
        width: 100%;
      }
      z-index: 0;  
      .dots{
        right: -8%;
      }
    }
    @include media-breakpoint-up(xl){
      .chat-image{
        width: 80%;
      }
      .dots{
        right: 0%;
      }
    }
  }
  .wave-container{
    .bg{
      margin-top: -2px;
      background-color: $white;
      min-height: 10vh;

    }
  }
}

.team {
  
  .team-sub-title{
    color: rgba($black, 0.7);
  }
  .team-description{
    color: rgba($black, 0.7);
  }
}
.counter{
  border-bottom: 1px solid #E7E9ED;
}
.work-process{
  
  p {
    color: rgba($black, 0.7);
  }
  .progress-item {
    .progress-bar, .progress-bar-2 {
      position: relative;
      z-index: 0;
      overflow: visible;
      .indicator-parent {
        display: inline-flex;
        height: 100%;
        
        transition: all 0.3s ease-in-out;
        .indicator {
          width: 73px;
          height: 73px;
          border-radius: map-get($border-radiuses, 'circle' );
          display: flex;
          justify-content: center;
          align-items: center;
        }
        &::before{
          content: '';
          position: absolute;
          height: 100%;
          border-left: 3px dashed #D5D7DD;
          // border-image: url('/assets/img/illustrations/border.png') 2 round;
          left: 50%;
          bottom: -50%;
          z-index: -1;
          @include media-breakpoint-up(sm){
            bottom: -70%;
          }
        }
        @include media-breakpoint-up(lg) {
          align-items: center;
          &::before{
            content: '';
            position: absolute;
            width: 100%;
            border-left: none;
            height: fit-content;
            border-bottom: 3px dashed #D5D7DD;
            border-image: url('/assets/img/illustrations/border.png') 20 round;
            left: 50%;
            bottom: 50%;
            z-index: -1;
          }
        }
      }
    }
    .progress-bar-3 {
      position: relative;
      z-index: 0;
      .indicator-parent {
        display: inline-flex;
        height: 100%;
        align-items: center;
        .indicator {
          width: 73px;
          height: 73px;
          border-radius: map-get($border-radiuses, 'circle' );
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
}

.experience{
  background: linear-gradient(to bottom,#313C59, #161C2D );
  
  p {
    color: rgba($white, 0.65);
  }
  .image-container{
    z-index: 0;
    max-height: 650px;
    .video{
      border-radius: 20px;
    }
    .overlay{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-color: $black;
      opacity: 0.1;
      z-index: 100;
      border-radius: 20px;
      transition: opacity 0.5s ease-in-out;
    }
    .play-button {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 50px;
      height: 50px;
      z-index: 999;
      padding: 0;
      border-radius: map-get($border-radiuses, 'circle');
      background-color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
      
      span{
        font-size: 15px;
        color: map-get($theme-colors, 'danger');
      }
      @include media-breakpoint-up(md){
        width: 80px;
        height: 80px;
      }
    }
    .dots{
        left: -5%;
        bottom: -5%;
        z-index: -1;
    }
  }
  .wave-container{
    min-height: 30vh;
    width: 100%;
    
    
    @include media-breakpoint-up(lg){
      min-height: 50vh;
    }
    .bg{
      min-height: 5vh;
      background: $white;
      @include media-breakpoint-up(md){
        min-height: 15vh;
      }
      @include media-breakpoint-up(lg){
        min-height: 20vh;
      }
      @include media-breakpoint-up(xl){
        min-height: 25vh;
      }

    }
    .wave{
      min-height: 10vh;
      background-position: right;
      background-size: cover;
      background-repeat: no-repeat;
      @include media-breakpoint-up(md){
        background-position: center;
        min-height: 5vh;
      }
      @include media-breakpoint-up(lg){
        min-height: 10vh;
      }
    }
  }
}

.testimonal{
  background:  #161C2D;
  .horizontal-rule{
    width: 161px;
    height: 3px;
    background-color: rgba($white, 0.15);
  }
  .heading{
    line-height: 1.4;
    letter-spacing: -1.2px;
  }
  .underline{
    position: relative;
    z-index: 0;
  }
  .underline::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 4px;
    left: 0;
    bottom: 20%;
    background-color: map-get($theme-colors, 'success' );
    z-index: -1;
  }

  .review-card-1, .review-card-2, .review-card-3{
    max-width: 730px;
    border-radius: 12px;
    border: 1px solid #57595E;
    color: rgba($black, 0.7);

    img{
      max-height: 111px;
    }
  }
  .review-card-1 {
   background-color: $white; 
  }
  .review-card-2{
    background-color: rgba($white, 0.70);
    transition: background-color 0.2s ease-in-out;
    @include hover-focus{
      background-color: $white;
    }
  }
  .review-card-3{
    background-color: rgba($white, 0.50);
    transition: background-color 0.2s ease-in-out;

    @include hover-focus{
      background-color: $white;
    }
  }

 

  .read-more{
    color: map-get($theme-colors, 'success' );
    transition: color 0.3s ease-in-out;
    gap: 10px;
    line-height: 2;
    &:hover, &:active {
      color: map-get($theme-colors , 'primary' );
      text-decoration: none;
    }
    .icon{
      margin-top: 3px;
    }
  }
}

.pricing{
  background-color: #F4F7FA;
  .description{
    color: rgba($black, 0.7);
  }
  .price-card-1, .price-card-2{
    border: 1px solid #E7E9ED;
    border-radius: 10px;
    .list{
      letter-spacing: 0.3px;
      padding: 21px 10px;
      border-top: 1px solid #E7E9ED;
    }
    button {
      margin-top: 7px;
    }
  }
  .price-card-1{
    background-color: transparent;
  }
  .price-card-2{
    background-color: $white;
    box-shadow: 0px 32px 64px 0px rgba(22, 28, 45, 0.07);
  }
  .price{ 

    .dollar {
      font-size: map-get($font-sizes, 6);
      font-weight: $font-weight-bold;
      line-height: $line-height-base;
      @include media-breakpoint-up(md){
        font-size: map-get($font-sizes, 7);
      }
    }
    .number {
      font-size: map-get($font-sizes, 2);
      font-weight: $font-weight-bold;
      line-height: $line-height-base;
      @include media-breakpoint-up(md){
        font-size: map-get($font-sizes, 1);
      }
    }
    .duration{
      font-size: map-get($font-sizes, 9 );
      line-height: $line-height-xl;
      font-family: "Rubik", sans-serif !important;
    }
  }

  @include media-breakpoint-up(md){
    min-width: 350px;
  }
}

.faq{
  .check-parent{
    background-color: map-get($theme-colors, 'success' );
    height: 30px;
    width: 30px;
    border-radius: map-get($border-radiuses, 'circle' );
  }
  .sub-title, .description{
    color: rgba($white, 0.65)
  }
  .accoridion-text{
    color: rgba($black, 0.7) !important;
  }
}
.subscription{
  background-color: #F4F7FA;
  
  .description{
    font-size: 15px;
    line-height: 1.7;
    color: rgba($black, 0.7);
  }
}